html, body {
  margin: 0;
  font-family: sans-serif;
}

.game {
  height: 100vh;
  -webkit-user-select: none;
  user-select: none;
  background: radial-gradient(circle, #61450a73 0% 40%, #000000ab 140%), url("cork-board.29410833.webp");
  justify-content: center;
  align-items: center;
  display: flex;
}

.game__container {
  position: relative;
}

.game__bag {
  width: 240px;
  height: 240px;
  background: url("delete.2335e6cc.svg");
  justify-content: center;
  align-items: center;
  margin: auto 60px auto 0;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 100%;
}

.game__bag--active {
  background: url("delete-active.86d2a49e.svg");
}

.game__area {
  background: #ffffff4d;
  box-shadow: 0 0 100px #0000001a;
}

